/*Navigation Bar CSS*/
            #navBar {
                list-style-type: none;
                margin: 0;
                padding: 0;
                overflow: hidden;
                background-color: #333;
            }
            
            .navDest {
                float:left;
            }
            
            .navName {
                display: block;
                color: white;
                text-align: center;
                padding: 14px 16px;
                text-decoration: none;
            }
            
            .navName:hover {
                background-color: #111;
            }