/*General image page CSS*/
            .picture{
                width: 300px;
                height: 300px;
            }

            /*icons*/

            i {
                float: left;
            }
            
            /*Light Box CSS*/
            /*Determines formatting of background when initiate light box*/
            .modal {
                    display: none; /* Hidden by default */
                    position: fixed; /* Stay in place */
                    z-index: 1; /* Sit on top */
                    left: 0;
                    top: 0;
                    width: 100%; /* Full width */
                    height: 100%; /* Full height */
                    overflow: auto; /* Enable scroll if needed */
                    background-color: black;
            }
            /* Determines the formatting of content box */
            .lightbox-content {
                    position: relative;
                    background-color: black/*#fefefe*/;
                    margin: auto;
                    padding: 0;
                    width: 100%;
            }
            
            #lightPic {
                width: 30%;
                height: 30%;
            }
            /* Determines the CSS formatting of the light box's close button. */
          .close {
                    color: #aaa;
                    float: right;
                    font-size: 28px;
                    font-weight: bold;
             }

          .close:hover,
          .close:focus {
                    color: black;
                    text-decoration: none;
                    cursor: pointer;
             }